.switch {
  display: inline-block;
  width: 64px;
  height: 32px;
  cursor: pointer;
  position: relative;
  background-color: rgba(0, 0, 0, 0.3) !important;
  border: 1px solid var(--bs-border-color);
}
.switch .slider {
  background: var(--bs-body-bg);
  width: 36px;
  height: 36px;
  position: absolute;
  top: -3px;
  left: -8px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid var(--bs-border-color);
  transition: left 0.4s ease-in-out;
}
.switch.round {
  border-radius: 32px;
}
.switch.round .slider {
  border-radius: 50%;
}

.switch-input {
  opacity: 0;
  width: 0;
  height: 0;
  appearance: none;
}
.switch-input:checked ~ .switch {
  background: #0d6efd;
}
.switch-input:checked ~ .switch .slider {
  left: 36px;
}

:root {
  --animate-duration: 1s;
  --animate-delay: 1s;
  --animate-repeat: 1;
}

@keyframes rubberBand {
  from {
    transform: scale3d(1, 1, 1);
  }
  30% {
    transform: scale3d(1.25, 0.75, 1);
  }
  40% {
    transform: scale3d(0.75, 1.25, 1);
  }
  50% {
    transform: scale3d(1.15, 0.85, 1);
  }
  65% {
    transform: scale3d(0.95, 1.05, 1);
  }
  75% {
    transform: scale3d(1.05, 0.95, 1);
  }
  to {
    transform: scale3d(1, 1, 1);
  }
}
.blast:hover {
  animation-name: rubberBand;
  animation-duration: 1s;
  animation-fill-mode: both;
}

html {
  font-size: 16px;
  line-height: 1.2;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  width: 100vw;
  margin: 0;
  background: #f8f9fa;
}
body[data-bs-theme=dark] {
  background: #212529;
}
@media (prefers-color-scheme: dark) {
  body {
    background: #212529;
  }
}

*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

/* Text meant only for screen readers. */
.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  word-wrap: normal !important;
}

.nav-pills {
  position: fixed;
  right: 1vw;
  z-index: 10;
  top: 50vh;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.nav-pills .pill {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 4px 0;
  padding: 6px;
}
.nav-pills .pill::after {
  content: "";
  display: inline-block;
  outline: 0;
  border: 2px solid rgba(123, 123, 123, 0.77);
  border-radius: 50%;
  padding: 6px;
  transition: border 0.6s ease-in-out;
}
.nav-pills .pill.active::after {
  border-color: rgba(202, 63, 63, 0.94);
  padding: 8px;
}
@media (max-width: 768px) {
  .nav-pills {
    display: none;
  }
}

header.fixed-top {
