}

/* nav links */
.navbar .nav-link {
  position: relative;
}
.navbar .nav-link:hover {
  font-weight: 700;
}
@media (min-width: 992px) {
  .navbar .nav-link::after {
    content: "";
    position: absolute;
    bottom: 0.125rem;
    background-color: #404040;
    border-radius: 50%;
    height: 0.125rem;
    left: 50%;
    right: 50%;
    transition: left 0.5s ease-in-out, right 0.5s ease-in-out;
  }
